:root{
    /* PRIMARY */
    --first-color: #BF8596;
    --input-color: #EDBFC0;
    --border-color: #F3DAC6;

    --body-font: 'Roboto', sans-serif;
    --normal-font-size: 1rem;
    --small-font-size: .75rem;
}

*, ::before, ::after{
    box-sizing: border-box;
}

.body{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-family: var(--body-font);
    font-size: 1rem;
    font-size: var(--normal-font-size);

    justify-content: center;
    align-items: center;
}

h1 {
    margin: 0;
}

.div-form {
    display: flex;
    flex-direction: column;

    /* margin: 4rem 2rem; */
    justify-content: center;
    align-items: center;
    /* height: 100vh; */
}

.form{
    width: 360px;
    padding: 1px 3px;
    border-radius: 1rem;
    box-shadow: 0 10px 25px rgba(92,99,105, .2);
}

.font-title{
    font-weight: 400;
    margin-bottom: 3rem;
}

.form_div{
    position: relative;
    height: 48px;
    margin-bottom: 1.5rem;
}

.form_input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    font-size: var(--normal-font-size);
    border: 1px solid #F3DAC6;
    border: 1px solid var(--border-color);
    border-radius: .5rem;
    outline: none;
    padding: 1rem;
    background-color: none;
    z-index: 1;
}

.form_label{
    position: absolute;
    left: 1rem;
    top: 1rem;
    padding: 0 .25rem;
    background-color: white;
    color: #EDBFC0;
    color: var(--input-color);
    font-size: 1rem;
    font-size: var(--normal-font-size);
    transition: .3s;
}

.form_button{
    display: block;
    margin-left: auto;
    padding: 1rem;
    outline: none;
    border: none;
    background-color: #BF8596;
    background-color: var(--first-color);
    color: white;
    font-size: 1rem;
    font-size: var(--normal-font-size);
    border-radius: .5rem;
    cursor: pointer;
    transition: .3s;
}

.form_button:hover{
    background-color: #BAABB4;
}

.form_input:focus + .form_label{
    top: -.5rem;
    left: .8rem;
    color: #BF8596;
    color: var(--first-color);
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
}

.form_input:not(:-ms-input-placeholder).form_input:not(:focus)+.form_label{
    top: -.5rem;
    left: .8rem;
    color: #BF8596;
    color: var(--first-color);
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
}

.form_input:not(:placeholder-shown).form_input:not(:focus)+.form_label{
    top: -.5rem;
    left: .8rem;
    color: #BF8596;
    color: var(--first-color);
    font-size: .75rem;
    font-size: var(--small-font-size);
    font-weight: 500;
    z-index: 10;
}

.form_input:focus{
    border: 1.5px solid #F3DAC6;
    border: 1.5px solid var(--border-color);
}
#navLink{
    border: 1px solid #F3DAC6;
    border: 1px solid var(--border-color);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: .5rem;
    font-size: 1rem;
    font-size: var(--normal-font-size);
    cursor: pointer;
    transition: .3s;
    padding: 0.5rem;
    text-align: center;
    display: block;
    margin-top: 5em;
    transition: .3s; 
}
#navLink:hover{
    background-color:#EDBFC0;
    background-color:var(--input-color);
}

